export default "import React from 'react';\r\nimport {makeData} from '../data/Utils';\r\n// Import React Table\r\nimport ReactTable from 'react-table';\r\n\r\nclass CustomColumnWidths extends React.Component {\r\n  constructor() {\r\n    super();\r\n    this.state = {\r\n      data: makeData(),\r\n    };\r\n  }\r\n\r\n  render() {\r\n    const {data} = this.state;\r\n    return (\r\n      <ReactTable\r\n        data={data}\r\n        columns={[\r\n          {\r\n            Header: 'Name',\r\n            columns: [\r\n              {\r\n                Header: 'First Name',\r\n                accessor: 'firstName',\r\n                maxWidth: 200,\r\n              },\r\n              {\r\n                Header: 'Last Name',\r\n                id: 'lastName',\r\n                accessor: (d) => d.lastName,\r\n                width: 300,\r\n              },\r\n            ],\r\n          },\r\n          {\r\n            Header: 'Info',\r\n            columns: [\r\n              {\r\n                Header: 'Age',\r\n                accessor: 'age',\r\n                minWidth: 400,\r\n              },\r\n            ],\r\n          },\r\n        ]}\r\n        defaultPageSize={10}\r\n        className='-striped -highlight'\r\n      />\r\n    );\r\n  }\r\n}\r\n\r\nexport default CustomColumnWidths;\r\n";